import ArticleMeta from './ArticleMeta'
import CommentContainer from './CommentContainer'
import React from 'react'
import agent from '../../agent'
import { connect } from 'react-redux'
import marked from 'marked'
import {
  ARTICLE_PAGE_LOADED,
  ARTICLE_PAGE_UNLOADED,
} from '../../constants/actionTypes'

const mapStateToProps = state => ({
  ...state.post,
  currentUser: state.common.currentUser,
})

const mapDispatchToProps = dispatch => ({
  onLoad: payload => dispatch({ type: ARTICLE_PAGE_LOADED, payload }),
  onUnload: () => dispatch({ type: ARTICLE_PAGE_UNLOADED }),
})

class Article extends React.Component {
  componentWillMount() {
    this.props.onLoad(
      Promise.all([
        agent.Posts.get(this.props.params.id),
        agent.Comments.forPost(this.props.params.id),
      ])
    )
  }

  componentWillUnmount() {
    this.props.onUnload()
  }

  render() {
    if (!this.props.post) {
      return null
    }

    const markup = { __html: marked(this.props.post.body, { sanitize: true }) }
    const canModify =
      this.props.currentUser &&
      this.props.currentUser.username === this.props.post.author.username
    return (
      <div className="article-page">
        <div className="banner">
          <div className="container">
            <h1>{this.props.post.title}</h1>
            <ArticleMeta post={this.props.post} canModify={canModify} />
          </div>
        </div>

        <div className="container page">
          <div className="row article-content">
            <div className="col-xs-12">
              <div dangerouslySetInnerHTML={markup} />
            </div>
          </div>

          <hr />

          <div className="article-actions" />

          <div className="row">
            <CommentContainer
              comments={this.props.comments || []}
              errors={this.props.commentErrors}
              slug={this.props.params.id}
              currentUser={this.props.currentUser}
            />
          </div>
        </div>
      </div>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Article)



